.transactions{
  padding: 4.125em 0;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.125em / 4));
    gap: .375em;
    margin-top: 2.25em;
  }
}

.transaction-card{
  padding: 1.5em 1.5em 1.25em;
  backdrop-filter: blur(54px);
  background: linear-gradient(180.00deg, rgba(21, 41, 43, 0.47),rgba(18, 28, 31, 0.99) 100%);
  border-radius: .6875em;
  display: flex;
  flex-direction: column;
  &__sum{
    font-size: 1.125em;
    line-height: 133%;
    font-weight: 700;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__date{
    color: #566c6d;
  }
  .pair__item-icon{
    width: 1.25em;
  }
}

.sell{
  padding: 7.75em 0 .25em;
  position: relative;
  z-index: 2;
  background-image: url(../img/sell-bg.svg);
  background-repeat: no-repeat;
  background-size: 51.75em auto;
  background-position: center top 0;
  .container{
    position: relative;
    z-index: 2;
  }
  &__bg{
    position: absolute;
  }
  &__bg1{
    top: 0;
    right: 0;
    width: 40.25em;
  }
  &__bg2{
    top: 10.75em;
    left: 0;
    width: 27em;
  }
  &__bg3{
    display: none;
  }
  &__inner{
    border-bottom: 1px solid rgba(35, 63, 62, 0.25);
    padding-bottom: 4.375em;
  }
}

.exchange{
  position: relative;
  margin: 3.125em auto 0;
  width: 41.375em;
  max-width: 100%;
  padding: 1.6875em;
  border-radius: 1.6875em;
  border: 1px solid rgba(#B2D7C4,.26);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    backdrop-filter: blur(27.066667556762695px);
    box-shadow: 0 83px 219px 0 rgba(8, 17, 13, 0.73);
    background: #0f1a1d;
    width: 100%;
    height: 100%;
    z-index: -1;
    border-radius: inherit;
  }
  &__top{
    .m-tooltip{
      margin-left: .625em;
    }
  }
  &__btn{
    margin-top: 1.125em;
    width: 100%;
    background: linear-gradient(108.64deg, rgb(4, 133, 79) -8.282%,rgb(0, 95, 56) 28.814%,rgb(0, 156, 91) 75.257%);
    height: 3.8125em;
    &:hover{
      background-size: 200% auto;
    }
    span{
      font-size: 1.125em;
    }
  }
  &__bottom{
    margin: 1.25em auto 0;
    max-width: 26.125em;
    text-align: center;
  }
  &__hint{
    font-size: .8125em;
    line-height: 138%;
    color: #476a6a;
    a{
      color: $white;
      &:hover{
        color: $primary;
      }
    }
  }
  &__error{
    font-size: .75em;
    color: #f96161;
    font-weight: 500;
    text-align: center;
    margin-top: 1em;
  }
}

.exchange-rate{
  display: flex;
  align-items: center;
  &:hover{
    .hint{
      opacity: 1;
      pointer-events: unset;
    }
  }
  label{
    margin: 0 .75em;
  }
  &__title{
    font-weight: 600;
    color: #798e93;
    position: relative;
    z-index: 2;
  }
  .hint{
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: -1px;
    padding-bottom: .75em;
    white-space: nowrap;
    color: $white;
  }
  input:not(:checked){
    &~.exchange-rate__title1{
      color: $white;
    }
  }
  input:checked{
    &~.exchange-rate__title2{
      color: $white;
    }
  }
  input:disabled{
    &~.exchange-rate__title2 .text14{
      opacity: .3;
    }
  }
}

.hint{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    padding: .25em .75em;
    background: #2e383d;
    border-radius: .5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%) rotate(45deg);
      bottom: -.1875em;
      background: inherit;
      width: .5em;
      height: .5em;
    }
  }
  .text12{
    line-height: 189%;
  }
}

.m-tooltip {
  position: relative;
  display: flex;
  align-items: center;
  &:hover {
    cursor: pointer;
    .m-tooltip__wrap {
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__icon{
    width: 1.75em;
  }
  &__wrap {
    z-index: 10;
    position: absolute;
    left: 0;
    top: 100%;
    padding: .5em 0 0;
    pointer-events: none;
    opacity: 0;
    transition: all .3s;
  }
  &__inner {
    width: 23.125em;
    padding: 1.5em 1em;
    box-shadow: 0 .875em 2.75em rgba(0, 0, 0, 0);
    background: #060e13;
    border-radius: .9375em;
  }
}

.course-info {
  display: flex;
  align-items: flex-start;
  @include lastM(0 0 1em);
  img,
  svg {
    width: 1.5em;
    margin-right: 1em;
  }
  .text13{
    line-height: 169%;
  }
}


@media screen and (max-width: 992px){
  .transactions{
    padding: 2.5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
      margin-top: 1.5em;
    }
  }

  .transaction-card{
    padding: .75em 1em;
    &__sum{
      font-size: .9375em;
      line-height: 160%;
    }
    &__bottom{
      padding-top: .5em;
    }
  }

  .sell{
    padding: 6.25em 0 0;
    &__inner{
      padding-bottom: 2.3125em;
    }
  }

  .exchange{
    margin-top: 3em;
    &__top{
      .m-tooltip{
        &__wrap{
          left: unset;
          right: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .sections-group{
    overflow: hidden;
  }

  .transactions{
    padding: 2em 0;
    position: relative;
    z-index: 3;
    &__items{
      grid-template-columns: 100%;
      gap: .375em;
    }
  }

  .sell{
    padding: 5.5em 0 .5em;
    &__bg,
    &__bg2{
      display: none;
    }
    &__bg3{
      top: 5em;
      left: 0;
      display: block;
      z-index: -1;
      width: 100%;
    }
    &__inner{
      padding-bottom: 1.5em;
    }
  }

  .exchange{
    padding: 1.6875em 1.25em 2.375em;
    max-width: unset;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    margin-top: 1.75em;
    border-radius: 1.25em;
    &__top{
      justify-content: center;
    }
    &__btn{
      margin-top: 2em;
      height: 3.125em;
      span{
        font-size: .875em;
      }
    }
    &__bottom{
      margin-top: 1.6875em;
      max-width: 18.125em;
    }
  }

  .exchange-rate{
    label{
      margin: 0 .5em;
    }
  }

  .m-tooltip{
    &__icon{
      width: 1.3125em;
    }
    &__inner{
      max-width: calc(100vw - 2.5em);
    }
  }
}
