@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.125em;
    height: 2.5em;
    border-radius: .5em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
    }
}

.m-btn-big{
    padding: 0 2.75em;
    height: 4em;
    border-radius: .5625em;
    span{
        font-size: .9375em;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: #cddbd7;
        color: $black;
    }
}

.m-btn-green{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: #00814b;
        color: $white;
    }
    &:disabled{
        background: #293135;
        color: rgba($white,.2);
    }
}

.m-btn-text{
    color: $white;
    &:hover{
        color: $primary;
        path{
            fill: $primary;
        }
    }
}

.m-btn-gray-outline{
    border: 1px solid #2d3335;
    color: $white;
    &:hover{
        background-color: rgba($primary,.2);
        border-color: rgba($primary,.2);
        color: $white;
    }
}


@media screen and (max-width: 992px){
    .m-btn-big{
        padding: 0 1.5em;
        height: 3.5em;
    }
}


