.exchanges{
  padding: 8em 0;
  position: relative;
  &__bg{
    position: absolute;
    top: 0;
    right: 0;
    width: 53em;
    z-index: -1;
  }
  .container{
    position: relative;
    z-index: 2;
  }
}

.filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:first-child{
    margin-top: 0;
  }
  .tabs{
    margin-left: 1.5em;
  }
  .tab{
    margin-top: 0;
  }
  &__tabs{
    display: flex;
  }
  &__btn{
    margin-left: .5em;
    height: 3.5em;
    padding: 0 1.5em;
    span{
      font-size: 1em;
    }
  }
  .m-select{
    margin-left: .5em;
    width: 15.5em;
    &:first-child{
      margin-left: 0;
    }
  }
}

.date-input{
  input{
    cursor: pointer;
    font-size: 1em;
    padding: 0 3.5em 0 1.5em;
    height: 3.5em;
    background-image: url(../img/arrow.svg);
    background-size: 1.5em auto;
    background-position: right 1.5em center;
    background-repeat: no-repeat;
    &._disabled{
      pointer-events: none;
    }
  }
}

.air-datepicker--pointer{
  display: none;
}

.air-datepicker{
  width: 20.5em;
  font-size: 1em;
  padding: 1.25em 1.5em;
  background: #292f32;
  color: $white;
  font-family: $font1;
  border: none;
  box-sizing: border-box;
  border-radius: .75em;
  &--content{
    padding: 0;
    margin-top: 4em;
  }
}

.air-datepicker-nav{
  padding: 0;
  border: none;
  &--title{
    font-size: 1em;
    line-height: 150%;
    font-weight: 700;
    &:hover{
      background-color: transparent;
    }
  }
  &--action{
    width: 2.25em;
    height: 2.25em;
    @include bgImgSize(100%);
    svg{
      display: none;
    }
    &:hover{
      background-color: transparent;
      @include bgImgSize(100%);
    }
    &[data-action="prev"]{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.5 23L15.5 18L20.5 13' stroke='%23808691' stroke-width='1.66667' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      &:hover{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.5 23L15.5 18L20.5 13' stroke='%23fff' stroke-width='1.66667' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      }
    }
    &[data-action="next"]{
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.5 23L20.5 18L15.5 13' stroke='%23808691' stroke-width='1.66667' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      &:hover{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='36' height='36' viewBox='0 0 36 36' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.5 23L20.5 18L15.5 13' stroke='%23fff' stroke-width='1.66667' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      }
    }
  }
}

.air-datepicker-body--day-names{
  margin: 0 0 .25em;
}

.air-datepicker-cell,
.air-datepicker-body--day-name{
  font-size: .875em;
  height: 2.85em;
  color: $white;
  text-transform: unset;
}

.air-datepicker-body--day-name{
  font-weight: 500;
}

.air-datepicker-body--cells.-days-{
  grid-auto-rows: unset;
  gap: .25em 0;
}

.air-datepicker-cell{
  border-radius: 1.5em;
}

.air-datepicker-cell.-selected-.-current-,
.air-datepicker-cell.-selected-{
  background: #008c52;
}

.air-datepicker-cell.-current-{
  color: #008c52;
}

.air-datepicker-cell.-year-.-other-decade-,
.air-datepicker-cell.-day-.-other-month-{
  color: #808691;
}

.air-datepicker-cell.-focus-{
  background: #3b4348;
  font-weight: 500;
}

.air-datepicker--buttons{
  padding: 1em;
  width: calc(100% + 3em);
  margin: 1.25em 0 -1.25em -1.5em;
  border-top: 1px solid #3d474c;
}

.air-datepicker-buttons{
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .75em;
  grid-auto-flow: unset;
}

.air-datepicker--buttons .datepicker-today,
.air-datepicker--buttons .button-apply,
.air-datepicker-button{
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  background: #2e383d;
  @include center;
  color: $white;
  border-radius: .5em;
  height: 2.5em;
  span{
    font-size: .875em;
  }
}

.air-datepicker--buttons .button-apply{
  color: $white;
  background: #008c52;
}


.air-datepicker--buttons .datepicker-today{
  position: absolute;
  top: 4.25em;
  left: 1.5em;
  width: calc(100% - 3em);
}


@media screen and (max-width: 992px){
  .exchanges{
    padding: 6.25em 0;
    &__bg{
      display: none;
    }
  }

  .filter{
    display: block;
    &__right{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .exchanges{
    padding: 4.5em 0 5.625em;
  }

  .filter{
    &__right{
      margin-top: 1.5em;
    }
    .m-select{
      width: auto;
    }
    &__btn{
      padding: 0 .875em;
      height: 2.5em;
      span{
        font-size: .8125em;
      }
    }
  }

  .date-input{
    input{
      font-size: .8125em;
      height: 3.07em;
      padding: 0 2em 0 .61em;
      border-radius: .61em;
      background-size: 1.53em auto;
      background-position: right .61em center;
    }
  }

  .air-datepicker{
    left: 1.25em !important;
  }
}
