.partners{
  overflow: hidden;
  position: relative;
  &__bg{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    width: 51.75em;
    z-index: 2;
  }
  &__bg2{
    position: absolute;
    left: 0;
    top: 12em;
    width: 25.75em;
  }
  &__bg3{
    position: absolute;
    right: 0;
    top: 0;
    width: 80.3125em;
  }
}

.partners-main{
  padding: 6.25em 0 4.375em;
  position: relative;
  &__content{
    position: relative;
    z-index: 3;
    max-width: 40em;
    @include grid100gap(1.5em);
  }
  &__desc{
    font-size: 1.125em;
    line-height: 139%;
    max-width: 29em;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    width: 44em;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.how-work{
  position: relative;
  z-index: 3;
  margin-top: 11.5em;
  &__grid{
    margin-top: 2.875em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.625em;
  }
  &__btn{
    width: 21.75em;
    height: 4.25em;
    max-width: 100%;
    span{
      font-size: 1em;
    }
  }
}

.step-card{
  padding: 0 2.5em;
  text-align: center;
  position: relative;
  &:last-child{
    &:after{
      display: none;
    }
  }
  &:after{
    content: '';
    position: absolute;
    top: 1.875em;
    transform: translateY(-50%);
    left: 50%;
    margin-left: 4em;
    height: 1px;
    width: 20.5em;
    background-color: #233c3c;
  }
  &__icon{
    width: 3.75em;
    height: 3.75em;
    margin: 0 auto;
    img{
      width: 1.875em;
    }
  }
  &__text{
    margin-top: 1em;
    @include grid100gap(1em);
  }
  &__title{
    font-weight: 600;
    font-size: 1.5em;
    line-height: 117%;
  }
  &__desc{
    color: #b5b6b6;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .partners{
    &__bg{
      display: none;
    }
  }

  .partners-main{
    padding: 6em 0 2.8125em;
    &__content{
      gap: 1em;
      max-width: 22.5em;
    }
    &__desc{
      font-size: 1em;
      line-height: 137%;
    }
    &__img{
      width: 25.3125em;
      top: 5em;
    }
  }

  .how-work{
    margin-top: 6em;
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
      margin: 2.25em auto 0;
      width: 32em;
      max-width: 100%;
    }
    &__nav{
      margin-top: 2.25em;
    }
    &__btn{
      height: 3.5em;
      span{
        font-size: .875em;
      }
    }
  }

  .step-card{
    text-align: left;
    display: flex;
    align-items: flex-start;
    padding: 0 0 1.75em;
    &:after{
      margin: 0;
      left: 1.5em;
      top: 3.75em;
      width: 1px;
      height: calc(100% - 3.75em);
      transform: translateX(-50%);
    }
    &__icon{
      margin: 0 3em 0 0;
      width: 3em;
      height: 3em;
      border-radius: .8125em;
      img{
        width: 1.5em;
      }
    }
    &__text{
      margin: 0;
      gap: .5625em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 140%;
      br{
        display: none;
      }
    }
    &__desc{
      font-size: .8125em;
      line-height: 154%;
    }
  }
}


@media screen and (max-width: 576px){
  .partners{
    &__bg2{
      width: 100%;
      top: 0;
    }
    &__bg3{
      display: none;
    }
  }

  .partners-main{
    padding: 5em 0 2.75em;
    &__img{
      position: static;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__desc{
      font-size: .875em;
      line-height: 157%;
    }
    &__img{
      width: calc(100% + 2.5em);
      margin: -5em 0 -3em -1.25em;
    }
  }

  .how-work{
    margin-top: 0;
    &__grid{
      margin-top: 1.875em;
      gap: 2em;
    }
    &__btn{
      width: 100%;
    }
  }

  .step-card{
    padding: 0;
    display: block;
    &:after{
      display: none;
    }
    &__icon{
      margin: 0 0 .75em;
    }
    &__title{
      font-size: 1.125em;
      line-height: 156%;
    }
  }
}
