.deal-payment{
  margin-top: .75em;
  display: grid;
  grid-template-columns: calc(100% - 16em) 13em;
  gap: 2.25em 3em;
  &__col{
    padding-top: 1.25em;
  }
}

.qr{
  padding: 2em 1.5em;
  background: #292f32;
  border-radius: .75em;
  max-width: 13em;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 8.5em;
    margin: 0 0 .5625em;
  }
  &__title{
    font-size: .9375em;
    line-height: 160%;
    font-weight: 600;
  }
}

.hint-block-payment{
  margin-top: 4.75em;
  .hint-block__text{
    font-size: .875em;
  }
}

.verification-modal{
  .modal__dialog{
    padding: 2em 2.5em;
    width: 73.125em;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__desc{
    margin-left: 1.25em;
    color: #74797D;
  }
  .close-btn{
    position: absolute;
    top: 1.25em;
    right: 1.875em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 20.375em calc(100% - 24.375em);
    gap: 2.25em 4em;
    margin-top: 2.875em;
  }
  &__col1{
    padding-top: 1em;
    padding-right: 2.5em;
    border-right: 1px solid #252D30;
  }
  &__col2{
    padding-top: 1em;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: .625em;
  }
  &__btn{
    padding: 0 3.25em;
    height: 4em;
    span{
      font-size: 1em;
    }
  }
}

.close-btn{
  width: 1.5em;
  @include center;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}

.verification-exchange{
  margin-top: 1.875em;
}

.exchange-item2{
  @include center;
  padding: .625em;
  min-height: 2.625em;
  border: 1px solid #2e383d;
  border-radius: .75em;
  font-weight: 500;
  position: relative;
  margin-bottom: 1.5em;
  &:after{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 100%;
    width: 1px;
    height: 1.375em;
    background-color: #333d42;
    margin-top: calc(.0625em + 1px);
  }
  &:last-child{
    margin-bottom: 0;
    &:after{
      display: none;
    }
  }
  &__icon{
    width: 1.25em;
    margin-right: .75em;
  }
  &__title{
    color: #808888;
    margin-right: .5em;
  }
  &__num{
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .deal-payment{
    margin-top: 1.375em;
    &__col{
      padding-top: 1.5em;
    }
    .requisite-item{
      &__desc{
        font-size: 1.125em;
      }
    }
  }

  .verification-modal{
    .modal__dialog{
      width: 47em;
      padding: 2em 2.25em;
    }
    .close-btn{
      top: 2em;
      right: 1.625em;
    }
    &__grid{
      margin-top: 2.25em;
      grid-template-columns: 100%;
    }
    &__col1{
      padding: 0;
      width: 21em;
      max-width: 100%;
      border: none;
    }
    &__col2{
      padding: 0;
    }
    &__btn{
      padding: 0 3.75em;
      height: 3.5em;
      span{
        font-size: .9375em;
      }
    }
  }

  .verification-exchange{
    margin-top: .75em;
  }

  .exchange-item2{
    padding: .375em;
    min-height: 2.125em;
    border-radius: .5em;
    &:after{
      height: .875em;
      margin-top: calc(.3125em + 1px);
    }
    &__title,
    &__num{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .deal-payment{
    margin-top: 1.75em;
    grid-template-columns: 100%;
    &__col{
      padding: 0;
    }
    .requisites__items{
      gap: 1.25em;
    }
  }

  .verification-modal{
    .modal__dialog{
      padding: 1.5em 1.25em;
      border-radius: .75em .75em 0 0;
    }
    .close-btn{
      top: 1em;
      right: 1em;
    }
    &__grid{
      gap: 2.5em;
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__btn{
      padding: 0;
      width: 100%;
    }
  }

  .close-btn{
    width: 1.25em;
  }
}
