.verification-list{
  margin-top: 2.5em;
  @include grid100gap(.75em);
}

.verification-card2{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .75em .75em .75em 1.5em;
  background: rgba(42, 49, 52, 0.62);
  border-radius: 1em;
  transition: all .3s;
  &__info{
    font-weight: 600;
    img{
      margin-right: 1em;
      width: 1.5em;
    }
  }
  &__btn{
    padding: 0 1.5em;
    ._view2{
      display: none;
    }
    img{
      width: 1.125em;
      margin-right: .5em;
    }
  }
  &._selected{
    background: #203b2f;
    .verification-card2__btn{
      background: #305244;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.verification-nav{
  display: flex;
  margin-top: 1.375em;
  &__btn{
    padding: 0 1em;
    svg{
      width: 1em;
      margin-right: .5em;
    }
  }
}

.verification-nav2{
  margin-top: 2.25em;
  display: flex;
  justify-content: flex-end;
  &__btn{
    height: 3em;
    padding: 0 3.5em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .verification-list{
    margin-top: 3em;
  }
}


@media screen and (max-width: 576px){
  .verification-list{
    margin-top: 2em;
  }

  .verification-card2{
    padding-left: .75em;
    &__info{
      img{
        width: 1.25em;
      }
    }
  }

  .verification-nav2{
    position: fixed;
    margin: 0;
    right: 1em;
    bottom: 1.125em;
    z-index: 10;
    &__btn{
      padding: 0 2em;
      height: 2.75em;
    }
  }
}
