.payment-props{
  padding: 1.5em;
  border: 1px solid #252e32;
  border-radius: 1.25em;
  margin-top: .9375em;
  max-width: 44.75em;
  @include grid100gap(1em);
  &__top{
    margin-top: 2.8125em;
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 150%;
  }
  &__title{
    color: #a0a4a8;
  }
  &__desc{
    font-weight: 600;
  }
}

.deal-thank__top,
.earn-money,
.deal-stopped{
  margin: 2.875em auto 0;
  width: 41em;
  max-width: 100%;
}

.deal-thank{
  padding: 8.5em 0 10em;
  &__top{
    background: #1d2427;
    border-radius: 1.375em;
    padding: 1.5em;
  }
  &__props{
    margin-top: 2em;
    @include grid100gap(1em);
    .prop-item__desc{
      font-weight: 500;
    }
  }
  &__nav{
    margin-top: 2.375em;
    display: flex;
    justify-content: center;
  }
  &__repeat{
    padding: 0 1em;
    height: 2.875em;
    border-radius: .8125em;
    svg{
      width: 1.5em;
      margin-right: .5625em;
    }
  }
}

.earn-money{
  margin-top: 1.25em;
  padding: 1.875em;
  background-color: #0e1517;
  background-image: url(../img/earn-money-bg.svg);
  background-size: cover;
  background-position: right bottom;
  border-radius: 1.5em;
  &__top{
    @include grid100gap(.625em);
    max-width: 25em;
  }
  &__title{
    font-size: 1.3125em;
    line-height: 138%;
    font-weight: 700;
    color: #16D283;
  }
  &__desc{
    font-size: .875em;
    line-height: 157%;
  }
  &__nav{
    display: flex;
    margin-top: 1.375em;
  }
  &__btn{
    padding: 0 1.125em;
  }
}

.deal-stopped{
  margin-top: 2.875em;
  background: #1d2427;
  padding: 1.5em;
  border-radius: 1.375em;
  text-align: center;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.375em;
  }
  &__btn{
    padding: 0 2em;
  }
}


@media screen and (max-width: 992px){
  .payment-props{
    &__top{
      margin-top: 2.25em;
    }
  }

  .prop-item{
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 130%;
    }
  }

  .deal-thank{
    padding: 6.25em 0 7em;
  }
}


@media screen and (max-width: 576px){
  .payment-props{
    padding: 1em .75em;
    border-radius: 1em;
    &__top{
      margin-top: 2.125em;
    }
  }

  .prop-item{
    @include grid100gap(.125em);
  }

  .deal-thank{
    padding: 4.5em 0 3.5em;
    .container{
      padding: 0;
    }
    &__top{
      margin-top: 2em;
    }
  }

  .earn-money{
    padding: 2.5em 1.25em 13em;
    background-image: url(../img/earn-money-bg2.svg);
    &__top{
      max-width: 19em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .deal-stopped{
    margin-top: 1.75em;
  }
}
