.payment-preparing{
  margin: 7em auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 28em;
  max-width: 100%;
  &__icon{
    margin-bottom: 1.25em;
    width: 2.875em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.requisites{
  margin-top: 2.375em;
  &__items{
    margin-top: 1.125em;
    display: grid;
    grid-template-columns: 13.5em calc(100% - 14em);
    gap: 1.75em .5em;
  }
  .hint-block{
    margin-top: 1.5em;
  }
}

.requisite-item{
  &__title{
    font-size: .9375em;
    line-height: 160%;
    color: #7b8184;
  }
  &__bottom{
    margin-top: .3125em;
  }
  &__desc{
    font-weight: 600;
    font-size: 1.375em;
    line-height: 109%;
    @include truncate(1);
  }
  .m-copy{
    margin-left: 1em;
  }
}

._full{
  grid-column: 1/-1;
}

.m-copy{
  position: relative;
  &._open{
    .hint{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    background: rgba(41, 47, 50, 0.5);
    width: 2em;
    height: 2em;
    @include center;
    border-radius: .4375em;
    img{
      width: 1em;
    }
  }
  .hint{
    position: absolute;
    left: 50%;
    bottom: 100%;
    transform: translateX(-50%);
    padding-bottom: .25em;
    &__inner{
      display: flex;
      align-items: center;
      background: #0b1011;
      height: 2.75em;
      padding: 0 1em 0 .625em;
      img{
        width: 1.5em;
        margin-right: .25em;
      }
    }
  }
}

.hint-block{
  padding: .875em;
  background: #222b2e;
  border-radius: .25em;
  color: #93aab8;
  font-weight: 500;
  text-align: center;
  &__text{
    font-size: .9375em;
    line-height: 160%;
  }
}

.payment-nav{
  display: flex;
  margin-top: 3em;
  &__btn{
    padding: 0 2.3125em;
    height: 3.75em;
    span{
      font-size: 1em;
    }
  }
  .payment-preparing{
    flex-direction: row;
    margin: 0;
    color: #d8d8d8;
    &__icon{
      width: 2em;
      margin: 0 1.1875em 0 0;
    }
  }
}


@media screen and (max-width: 992px){
  .payment-preparing{
    margin-top: 5.375em;
  }

  .requisites{
    margin-top: 3.25em;
    &__items{
      row-gap: 1.5em;
    }
    .hint-block{
      margin-top: 2.5em;
    }
  }

  .hint-block{
    &__text{
      font-size: .875em;
      line-height: 171%;
    }
  }

  .payment-nav{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .payment-preparing{
    margin-top: 4.5em;
  }

  .requisites{
    margin-top: 2em;
    &__items{
      grid-template-columns: 100%;
      gap: .875em;
      margin-top: .875em;
    }
    .hint-block{
      margin-top: 2.25em;
    }
  }

  .requisite-item{
    &__desc{
      font-size: 1.125em;
    }
  }

  .hint-block{
    &__text{
      font-size: .8125em;
      line-height: 131%;
    }
  }

  .payment-nav{
    margin-top: 1.625em;
  }

  .m-copy{
    .hint{
      position: fixed;
      bottom: 6.25em;
      &__inner{
        justify-content: center;
        height: 3.625em;
        width: calc(100vw - 2.5em);
        &:before{
          display: none;
        }
      }
      .text14{
        font-size: 1em;
      }
    }
  }
}
