.activate-account{
  padding-top: 7.5em;
  &__bottom{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__btn{
    margin-top: 1em;
    padding: 0 2.375em;
  }
  .m-captcha{
    margin-top: 2em;
  }
}

.info-block{
  width: 29em;
  max-width: 100%;
  margin: 0 auto;
  &._type3,
  &._type2{
    width: 64em;
    .info-block__icon{
      background: #1e2427;
      border: none;
      box-shadow: unset;
      &:before,
      &:after{
        display: none;
      }
    }
  }
  &._type3{
    .info-block__icon{
      width: 4.75em;
      height: 4.75em;
      img{
        width: 2.8125em;
      }
    }
  }
  &__icon{
    position: relative;
    overflow: hidden;
    border: 1px solid rgba(#B2D7C4,.26);
    width: 3.5em;
    height: 3.5em;
    @include center;
    box-sizing: border-box;
    border-radius: 1em;
    box-shadow: 0 .875em 4em rgba(0, 0, 0, 0.07);
    background: linear-gradient(180.00deg, rgb(39, 96, 68),rgb(26, 41, 43) 100%);
    margin: 0 auto 1.875em;
    flex-shrink: 0;
    &:before,
    &:after{
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 5.3125em;
      height: 2.5em;
      filter: blur(34px);
      background: rgba(13, 207, 126, 0.22);
    }
    &:before{
      top: -1.25em;
    }
    &:after{
      bottom: -1.25em;
    }
    svg,
    img{
      width: 1.9375em;
      position: relative;
      z-index: 2;
    }
  }
  &__links{
    margin-top: 1.5em;
    display: flex;
    justify-content: center;
  }
  &__desc{
    text-align: center;
    margin-top: 1em;
    font-weight: 500;
    font-size: .875em;
    line-height: 136%;
    color: #808888;
  }
  &__link{
    color: $primary;
    text-decoration: underline;
  }
}

.pass-recovery{
  padding-top: 12.5em;
  &__bottom{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2em;
  }
  &__btn{
    padding: 0 1.5em;
  }
}


@media screen and (max-width: 992px){
  .pass-recovery,
  .activate-account{
    padding-top: 3em;
  }
}


@media screen and (max-width: 576px){
  .pass-recovery,
  .activate-account{
    padding-top: 3.75em;
  }

  .info-block{
    width: 100%;
    &._type2{
      .info-block__icon{
        width: 4em;
        height: 4em;
        img{
          width: 2.375em;
        }
      }
    }
  }
}
