.exchange-info{
  margin-top: 1.625em;
  &__rate{
    .circle-progress{
      margin-right: .75em;
    }
  }
}

.circle-progress{
  position: relative;
  &__fill{
    position: absolute;
    top: 0;
    left: 0;
  }
  svg{
    width: 2.8125em;
    transform: rotate(-90deg);
  }
  &__num{
    @include centerAbsolute;
    @include center;
    font-weight: 800;
    width: 100%;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .exchange-info{
    margin-top: 1.5em;
  }

  .circle-progress{
    svg{
      width: 2.5em;
    }
  }
}
