.breadcrumbs {
    margin-bottom: 3.125em;
    font-weight: 500;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: $white;
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: .25em;
                right: 100%;
                margin-right: .625em;
                font-size: .875em;
                line-height: 130%;
                font-weight: 500;
            }
        }
        &:last-child{
            a{
                color: rgba($white, 0.5);
            }
        }
    }
    a{
        font-size: .875em;
        line-height: 130%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        margin-bottom: 2.5em;
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        margin-bottom: 2.1875em;
    }
}
