.fg {
  position: relative;
  margin: 0;
  label{
    color: rgba($white,.4);
    display: block;
    font-size: .875em;
    line-height: 162%;
    position: absolute;
    top: .71em;
    left: 1.71em;
    pointer-events: none;
    transform: translateX(1px);
    ._view2{
      display: none;
    }
  }
  input,
  textarea{
    border-radius: .67em;
    border: 1px solid #1b282b;
    padding: 1.125em 1em 0;
    height: 3.375em;
    width: 100%;
    font-size: 1.5em;
    line-height: 160%;
    background-color: #1b282b;
    color: #fff;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    &::placeholder {
      font-weight: 500;
      color: #7D888B;
    }
    &:focus{
      border-color: $primary;
    }
    &:disabled{
      //background-color: $white-smoke;
    }
    &._filled{
      &~label{
        //color: $cadet-gray;
      }
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: #823E3B;
      background: rgba(#F83E3E,.05);
    }
    .error-text{
      display: block;
    }
  }
  .error-text{
    font-size: .75em;
    margin-top: .5em;
    display: none;
  }
  &__btn{
    font-family: $font3;
    position: absolute;
    top: .25em;
    right: .25em;
    height: calc(100% - .5em);
    padding: 0 1.5em;
    border: 2px solid #454d51;
    border-radius: .75em;
    @include center;
    color: $white;
    font-weight: 500;
    &:hover{
      background-color: #454d51;
    }
    span{
      font-size: 1.125em;
    }
  }
}

.fg-dynamic{
  label{
    position: absolute;
    top: 1.125em;
    left: 1em;
    transition: all .2s;
    pointer-events: none;
    font-size: 1em;
    line-height: 125%;
  }
  textarea,
  select,
  input{
    font-size: 1em;
    line-height: 125%;
    padding-left: 1em;
    padding-right: 1em;
    height: 3.5em;
    &._filled,
    &:focus{
      &~label{
        top: .67em;
        left: 1.33em;
        font-size: .75em;
      }
    }
  }
  textarea{
    height: 7.5em;
    padding-top: 1.75em;
  }
}

.fg-light{
  textarea,
  input{
    background-color: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.05);
  }
}

.fg-light2{
  input{
    background-color: #292f32;
    border-color: #292f32;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1.25em;
  right: 1.5em;
  width: 1.5em;
  height: 1.5em;
  img{
    @include centerAbsolute;
    width: 100%;
  }
  ._view2{
    opacity: 0;
  }
  &._active{
    ._view2{
      opacity: 1;
    }
    ._view1{
      opacity: 0;
    }
  }
}

.score-field,
.file-input,
.m-switch,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-switch{
  display: flex;
  label{
    width: 2.6875em;
    height: 1.6875em;
    border-radius: .875em;
    background-color: rgba(50, 75, 64,.14);
    display: flex;
    align-items: center;
    padding: 0 .1875em;
    transition: all .3s;
    overflow: hidden;
    &:before{
      content: '';
      transition: inherit;
      width: 1.125em;
      height: 1.125em;
      border-radius: 50%;
      box-shadow: 0 .0625em 1.5em rgba(69, 223, 159, 0.4);
      background: #43e19f;
    }
  }
  input:checked~label{
    &:before{
      transform: translateX(1.1875em);
    }
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.5em;
    min-height: 1.375em;
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 170%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1.5px solid #008c52;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: rgba(#008C52,.9);
        background-size: .625em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .375em;
      background-image: url(../img/check5.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .fg{
    &:not(.fg-dynamic):not(.date-input):not(.currency-dropdown__search){
      input{
        height: 4em;
        font-size: 1em;
        line-height: 100%;
        padding: 2em 1em 1em;
      }
      label{
        font-size: .8125em;
        line-height: 108%;
        left: 1.23em;
        top: .92em;
      }
    }
    &__btn{
      padding: 0 .75em;
      span{
        font-size: .875em;
      }
    }
  }

  .m-switch--small{
    label{
      width: 1.75em;
      height: 1.0625em;
      border-radius: .5625em;
      padding: 0 .125em;
      &:before{
        width: .6875em;
        height: .6875em;
      }
    }
    input:checked~label{
      &:before{
        transform: translateX(.75em);
      }
    }
  }

  .m-check{
    label{
      span{
        font-size: .8125em;
      }
    }
  }
}
