.footer{
  padding: 3em 0 1.875em;
  border-top: 1px solid #132326;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__bg{
    pointer-events: none;
    position: absolute;
    top: -1.375em;
    right: 0;
    width: 88em;
    filter: blur(95.9000015258789px);
  }
  &__logo{
    display: block;
    width: 9em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    margin-top: 1.6875em;
    display: grid;
    grid-template-columns: 33.125em 26.5em;
    justify-content: space-between;
    gap: 3.5em 2em;
  }
  .socials{
    margin-top: 1.25em;
  }
  &__bottom{
    margin-top: 1.25em;
    padding-top: 1.5em;
    border-top: 1px solid rgba($white, 0.04);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-weight: 500;
    color: #6f898f;
  }
  &__c{
    margin-right: auto;
  }
  &__link{
    letter-spacing: -0.01em;
    margin-left: 1.5em;
  }
}

.footer-nav{
  display: flex;
  &__group{
    @include lastM(0 5.25em 0 0);
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1em;
  }
  &__link{
    margin-top: 1em;
    font-size: 1em;
    line-height: 101%;
    font-weight: 500;
    color: #8ea4a9;
  }
}

.footer-partners{
  margin-top: 6.25em;
  &__items{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(12,calc(100% / 12 - .1875em * 11 / 12));
    gap: 0 .1875em;
  }
}

.partner-item{
  height: 3.5em;
  @include center;
  img{
    max-width: 100%;
    max-height: 100%;
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__top{
      margin-top: 1.875em;
      grid-template-columns: 21em calc(100% - 23em);
    }
    .socials{
      margin-top: 6.625em;
    }
    &__bottom{
      margin-top: 2.25em;
    }
    &__link{
      margin-left: 1em;
    }
  }

  .footer-nav{
    &__group{
      @include lastM(0 3.875em 0 0);
    }
    &__title{
      font-size: 1em;
    }
    &__link{
      font-size: .875em;
      line-height: 115%;
      margin-top: 1.21em;
    }
  }

  .footer-partners{
    margin-top: 3.125em;
    &__items{
      grid-template-columns: repeat(7,calc(100% / 7 - .1875em * 6 / 7));
    }
  }

  .partner-item{
    height: 3em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 1.125em;
    &__bg{
      display: none;
    }
    &__top{
      grid-template-columns: 100%;
      position: relative;
      padding-bottom: 4.25em;
    }
    .socials{
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0;
    }
    &__bottom{
      margin-top: 1.75em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__c{
      order: 2;
      margin-top: 1em;
    }
    &__link{
      margin: 0 0 .33em;
    }
  }

  .footer-partners{
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - .1875em * 2 / 3));
    }
  }

  .partner-item{
    height: 3.125em;
  }
}
