.error404{
  background-image: url(../img/404-bg.jpg);
  background-size: cover;
  background-position: center top;
  flex-grow: 1;
  min-height: $vh;
  padding: 12.5em 1.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @media screen and (min-width: 993px){
    font-size: calc(100vw * (16 / 1440));
  }
  &__num{
    font-size: 9.375em;
    line-height: 112%;
  }
  &__desc{
    width: 40em;
    max-width: 100%;
    margin: .75em auto 0;
    .text18{
      line-height: 150%;
    }
  }
  &__link{
    margin-top: 3em;
    padding: 0 1.75em;
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding: 15em 1.25em;
    background-image: url(../img/404-bg2.jpg);
    &__num{
      font-size: 7.5em;
    }
    &__desc{
      width: 28em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    padding: 13em 1.25em;
    background-image: url(../img/404-bg3.jpg);
    &__num{
      font-size: 5em;
    }
    &__desc{
      width: 18.75em;
    }
  }
}
