.partners-info{
  margin-top: 3em;
  padding: 1em 1em 1em 2.625em;
  background: #1e2427;
  border-radius: 1.75em;
  display: grid;
  grid-template-columns: calc(100% - 38.5em) 37.125em;
  gap: 1.375em;
  &__col{
    padding-top: 1.875em;
  }
  &__col2{
    padding: 1.625em 1.75em 1.375em;
    background: #272c2f;
    border-radius: 1.25em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__text{
    @include grid100gap(.25em);
  }
  &__btn{
    padding: 0 1em;
    &:disabled{
      background: #3a4044;
      color: rgba(255, 255, 255, 0.4);
    }
  }
  &__bottom{
    margin-top: 1.375em;
    padding-top: .875em;
    border-top: 1px solid #2e3437;
    display: flex;
    align-items: flex-start;
  }
  &__item{
    margin-right: 1.625em;
    @include grid100gap(.25em);
  }
  &__link{
    margin-top: .625em;
    display: flex;
    align-items: center;
    &-text{
      @include truncate(1);
      font-weight: 700;
      font-size: 1.5625em;
      line-height: 128%;
      color: #a6ffaf;
    }
  }
  .m-copy{
    margin-left: 1em;
    &__btn{
      width: 3em;
      height: 3em;
      border-radius: .75em;
      img{
        width: 1.5em;
      }
    }
  }
}

.exchanges-history{
  margin-top: 3em;
  .filter{
    margin-top: 1.75em;
  }
  .tab2{
    padding: .25em 0 .75em;
    span{
      font-size: 1.125em;
    }
  }
  .tab-content{
    margin-top: 2em;
  }
  .info-block{
    margin-top: 5.375em;
  }
}

.program-table{
  th,td{
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(5){
      width: 13.5em;
      padding-right: 1em;
    }
    &:nth-child(5){
      padding-right: 0;
      padding-left: 1em;
      text-align: right;
    }
  }
}

.program-table2{
  th,td{
    width: 25%;
    padding-right: 1em;
    &:last-child{
      padding-right: 0;
      text-align: right;
    }
  }
}


@media screen and (max-width: 992px){
  .partners-info{
    padding: 1.5em;
    grid-template-columns: 100%;
    &__col2{
      padding: 1.5em 1.75em 1.375em;
    }
    &__bottom{
      padding-top: 1em;
    }
    &__item{
      margin-right: 2em;
    }
    &__link{
      &-text{
        font-size: 1.25em;
      }
    }
  }

  .exchanges-history{
    margin-top: 4em;
    .filter{
      margin-top: 1.375em;
    }
    .info-block{
      margin-top: 6.625em;
    }
  }

  .program-table{
    .m-table__wrap{
      min-width: 69.5em;
    }
    th,td{
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(4),
      &:nth-child(5){
        width: 11em;
      }
    }
  }

  .program-table2 {
    .m-table__wrap {
      min-width: 45em;
    }
  }
}


@media screen and (max-width: 576px){
  .partners-info{
    margin-top: 1.5em;
    gap: 1.25em;
    border-radius: 1.25em;
    &__col2{
      padding: 1.5em 1.25em;
    }
    &__top{
      @include grid100gap(1.25em);
    }
    &__bottom{
      margin-top: 2em;
    }
    &__link{
      &-text{
        font-size: .8125em;
      }
    }
    .m-copy{
      &__btn{
        width: 2.25em;
        height: 2.25em;
        border-radius: .625em;
        img{
          width: 1em;
        }
      }
    }
  }

  .exchanges-history{
    margin-top: 2.875em;
    .filter{
      margin-top: 1.125em;
    }
    .tab2{
      span{
        font-size: 1em;
      }
    }
    .info-block{
      margin-top: 4.375em;
    }
  }

  .program-table {
    .m-table__wrap {
      min-width: 68.25em;
    }
  }
}
