.text{
  p,li{
    font-size: 1em;
    line-height: 156%;
  }
  ul,
  p{
    @include lastM(0 0 1.375em);
  }
  ul{
    @include grid100gap(.25em);
    padding-left: 1em;
    li{
      position: relative;
      padding-left: 1.375em;
      &:before{
        content: '';
        position: absolute;
        top: .5625em;
        left: 0;
        width: .375em;
        height: .375em;
        border-radius: 50%;
        background-color: $primary;
      }
    }
  }
  h2{
    font-weight: 600;
    font-size: 1.625em;
    line-height: 123%;
    margin-bottom: .97em;
  }
  hr{
    display: block;
    margin: 1.375em 0;
    opacity: 0;
  }
  a{
    color: $primary;
    text-decoration: underline;
    &:hover{
      text-decoration-color: transparent;
    }
  }
}


@media screen and (max-width: 992px){
  .text{
    h2{
      font-size: 1.5em;
    }
    p,li{
      font-size: .875em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    h2{
      font-size: 1.25em;
    }
    p,li{
      font-size: .875em;
      line-height: 150%;
    }
  }
}
