.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #0C1417;
    opacity: .85;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 4em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        box-shadow: 0 .25em 1.875em rgba(0, 0, 0, 0.06);
        background: #1f2629;
        width: 27.125em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 2em;
        border-radius: 1.25em;
        display: inline-block;
    }
    &__title{
        font-size: 1.5em;
        line-height: 130%;
        font-weight: 700;
    }
    &__props{
        margin-top: 2em;
        @include grid100gap(2em);
    }
    &__nav{
        margin-top: 3.5em;
        display: flex;
        flex-direction: column;
        align-items: center;
        .line-btn{
            font-size: .9375em;
            margin-top: 1.25em;
        }
    }
    &__btn{
        height: 4em;
        width: 100%;
        span{
            font-size: 1em;
        }
    }
}

.modal-prop{
    font-weight: 500;
    @include grid100gap(.25em);
    &__title{
        font-size: .9375em;
        line-height: 160%;
        color: $gray;
    }
    &__desc{
        font-size: 1.125em;
        line-height: 133%;
    }
}

.withdraw-modal{
    .modal__dialog{
        width: 55em;
        padding: 1.75em 1.5em;
    }
    .modal__title{
        text-align: center;
        font-size: 1.75em;
    }
    .hint-block{
        margin-top: 2.375em;
        border-radius: .625em;
        &__text{
            font-size: .875em;
            line-height: 121%;
        }
    }
}

.withdraw-form{
    margin-top: 1.75em;
    &__fields{
        @include grid100gap(1em);
    }
    .fg{
        input{
            background: #2a3134;
        }
    }
    &__nav{
        display: flex;
        justify-content: center;
        margin-top: 2.8125em;
    }
    &__btn{
        padding: 0 2.25em;
        height: 3.25em;
    }
}

.created-modal{
    .modal__dialog{
        width: 30em;
        padding: 5.25em 3em 2.75em;
    }
    .close-btn{
        width: 1.25em;
        position: absolute;
        top: 1.75em;
        right: 1.75em;
    }
    &__icon{
        width: 3.75em;
        display: block;
        margin: 0 auto 2.75em;
    }
    &__desc{
        margin-top: .875em;
    }
    .modal__nav{
        margin-top: 3.125em;
    }
    .modal__btn{
        padding: 0 3.1875em;
        width: auto;
        height: 3.25em;
        span{
            font-size: .875em;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }

    .withdraw-modal{
        .modal__dialog{
            width: 47em;
            padding: 1.75em 2.815em 2.5em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {

        }
    }

    .withdraw-modal{
        .modal__dialog{
            padding: 2.8125em 1em 3.75em;
        }
        .hint-block{
            margin-top: 2.75em;
            padding: .875em .75em;
            &__text{
                font-size: .8125em;
                line-height: 131%;
            }
        }
    }

    .withdraw-form{
        margin-top: 1.375em;
        &__nav{
            margin-top: 2.375em;
        }
    }

    .created-modal{
        .modal__dialog{
            padding: 5.625em 1.5em 2.1875em;
        }
        .close-btn{
            top: 1.25em;
            right: 1.25em;
        }
        &__icon{
            margin-bottom: 3.5em;
        }
        &__desc{
            margin-top: 1em;
        }
        .modal__nav{
            margin-top: 2.5em;
        }
    }
}
