.exchange-items{
  margin-top: 1.25em;
  @include grid100gap(.375em);
  position: relative;
  &__arrows{
    @include centerAbsolute;
    @include center;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.02);
    background: #2b3c40;
    border-radius: .8125em;
    width: 2.8125em;
    height: 2.8125em;
    img{
      width: 1.4375em;
    }
  }
}

.exchange-item{
  position: relative;
  .currency-select{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    border-left: 1px solid #2b3a3e;
  }
  &__input{
    input{
      padding-right: 8em;
    }
  }
  &._open{
    z-index: 4;
    &~.exchange-items__arrows{
      opacity: 0;
    }
    .currency-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.currency-select {
  z-index: 2;
  width: 11.5em;
  height: 4.5em;
  position: relative;
  padding: 0 2.125em 0 1.75em;
  display: flex;
  align-items: center;
  cursor: pointer;
  &__icon {
    width: 1.75em;
    margin-right: .5em;
  }
  &__name{
    font-weight: 700;
    padding-right: .25em;
  }
  &__arrow {
    margin-left: auto;
    width: 1.5em;
    transition: all .3s;
  }
}

.currency-dropdown,
.city-dropdown {
  z-index: 4;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  border-radius: 1em;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: .125em;
  width: 100%;
  padding: 1em 1em 0;
  box-shadow: 0 1.5em 4em rgba(0, 0, 0, 0.01);
  background-color: #243235;
  &__inner {
    margin-top: .875em;
    max-height: 15.5em;
    overflow-y: auto;
    @include scroll;
    width: calc(100% + .625em);
  }
  &__items {
    padding-bottom: 1.5em;
    padding-right: 1.5em;
  }
}

.currency-dropdown {
  .tabs {
    max-width: 36em;
    width: 100%;
  }
  &__search {
    margin: 1.25em 0 .875em;
    input {
      font-size: .875em;
      height: 3em;
      padding: 0 1em 0 3.14em;
      border-radius: .71em;
      font-weight: 600;
      border-color: #314043;
      background-color: #314043;
      background-image: url(../img/search-icon.svg);
      background-size: 1.36em auto;
      background-position: left 1em center;
      background-repeat: no-repeat;
    }
  }
  &__bg,
  &__close{
    display: none;
  }
}

.currency-item{
  display: flex;
  align-items: center;
  padding: .5em;
  transition: all .3s;
  font-weight: 500;
  &:hover{
    color: $primary;
    cursor: pointer;
  }
  &__icon{
    width: 1.5625em;
    margin-right: .5em;
  }
  &__desc{
    margin-left: auto;
    padding-left: .25em;
  }
  &._disabled{
    pointer-events: none;
    color: #545e60;
    .currency-item__icon{
      opacity: .4;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .exchange-items{
    gap: .5em;
    &__arrows{
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1.25em;
      }
    }
  }

  .exchange-item{
    &__input{
      input{
        padding-right: 10em;
      }
    }
    &._open{
      .currency-dropdown__modal{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  ._open-currency-modal{
    .header{
      z-index: 1;
    }
    .sell{
      z-index: 5;
    }
  }

  .currency-dropdown{
    position: relative;
    top: unset;
    margin: 0;
    border-radius: .75em .75em 0 0;
    overflow-y: auto;
    height: 100%;
    padding: 1.5em 1.25em;
    z-index: 5;
    opacity: 1;
    pointer-events: unset;
    &__modal{
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      z-index: 20;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      padding-top: 4.875em;
    }
    &__bg{
      background: #0C1417;
      opacity: .8;
      display: block;
    }
    &__close{
      z-index: 5;
      position: absolute;
      top: 2.75em;
      right: 1.25em;
      width: 1.25em;
      background-color: transparent;
      @include center;
      img{
        width: 100%;
      }
    }
    &__inner{
      max-height: unset;
      width: 100%;
    }
    &__items{
      padding-right: 0;
    }
  }

  .currency-item{
    padding-left: 0;
    padding-right: 0;
  }

  .currency-select{
    height: 2.75em;
    padding: 0 1.25em 0 .875em;
    width: 9em;
    &__icon{
      width: 1.25em;
      margin-right: .625em;
    }
    &__arrow{
      width: 1em;
    }
  }
}
