.m-dropdown{
  position: relative;
  &._open{
    z-index: 5;
    .m-dropdown__arrow,
    .m-dropdown__arrow,
    .m-dropdown__show svg{
      transform: rotate(-180deg);
    }
    .m-dropdown__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    font-weight: 500;
    cursor: pointer;
    text-transform: unset;
    display: flex;
    align-items: center;
  }
  &__arrow{
    width: 1em;
    margin-left: 1.6875em;
    transition: all .3s;
    &:first-child{
      margin-left: 0;
    }
  }
  &__hidden{
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    padding: 1.5em;
    left: 50%;
    transform: translateX(-50%);
    top: 100%;
    margin-top: .25em;
    background-color: $dark;
    box-shadow: $shadow;
    border-radius: 1em;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    @include lastM(0 0 1em);
    font-size: 1em;
    line-height: 150%;
    img{
      width: 1.5em;
    }
    &--border{
      margin-top: .5em;
      padding-top: 1.5em;
      border-top: 1px solid #2e383d;
      width: 100%;
    }
  }
}

.lang{
  .m-dropdown__arrow{
    margin-left: .5em;
  }
}

._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.header,
.auth{
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 8.3125em;
    margin-right: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: .875em 0;
  .container{
    max-width: 1440px;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .lang{
    margin-right: 2.125em;
  }
  .socials{
    display: none;
  }
}

.is-online{
  display: flex;
  align-items: center;
  background: rgba(13, 25, 27, 0.7);
  border: 1px solid rgba(19, 38, 41, 0.8);
  border-radius: 3.75em;
  padding: 0 .875em;
  height: 2em;
  color: #a6ffaf;
  font-weight: 500;
  &:before{
    content: '';
    width: .375em;
    height: .375em;
    flex-shrink: 0;
    border-radius: 50%;
    margin-right: .375em;
    background-color: #20c784;
    box-shadow: 0 .0625em 1.25em rgba(69, 223, 159, 0.4);
  }
  &._offline{
    background: rgba(36, 43, 45, 0.6);
    border: none;
    color: rgba(165, 165, 165, 0.8);
    &:before{
      display: none;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link {
    font-size: .875em;
    line-height: 114%;
    transition: all .3s;
    @include lastM(0 1.86em 0 0);
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    &--active{
      color: $primary;
    }
  }
}

.header-user{
  .m-dropdown__show{
    font-weight: 700;
  }
  &__icon{
    width: 1em;
    margin-right: .375em;
  }
  .m-dropdown__hidden{
    min-width: 16.5em;
    left: unset;
    transform: unset;
    right: 0;
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      position: relative;
    }
    &__logo{
      width: 8.75em;
    }
    &__login{
      height: 2em;
      padding: 0 1.3125em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    @media screen and (min-width: 576px){
      position: absolute;
      top: 100%;
      right: -1.25em;
      margin-top: .5em;
      z-index: 5;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      padding: 1.5em;
      background-color: $dark;
      box-shadow: $shadow;
      border-radius: 1em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      font-size: 1em;
      line-height: 150%;
      @include lastM(0 0 1em);
    }
  }

  .header-action{
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 3.5em;
      height: 1.6875em;
      width: 1px;
      background-color: $dark;
    }
  }

  .burger{
    flex-shrink: 0;
    margin-left: 4em;
    display: flex;
    padding: 0;
    width: 1.5em;
    position: relative;
    z-index: 21;
    ._view2{
      opacity: 0;
    }
    img{
      @include centerAbsolute;
      width: 100%;
      transition: all .3s;
    }
  }

  .header-user{
    .m-dropdown__show{
      padding: 0;
      width: 2.5em;
      height: 2.5em;
    }
    .m-dropdown__arrow,
    .m-dropdown__show span{
      display: none;
    }
    &__icon{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  ._hide-mob{
    display: none;
  }

  .header{
    padding: .625em 0;
    &__logo{
      width: 6.75em;
      margin-right: .5em;
      position: relative;
      z-index: 21;
    }
    &__login{
      height: 1.75em;
      padding: 0 .75em;
      border-radius: .375em;
      span{
        font-size: .75em;
      }
    }
    .lang{
      position: absolute;
      top: 3.875em;
      right: .25em;
      z-index: 22;
      margin: 0;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    .socials{
      display: flex;
      margin-top: 5em;
    }
    &._open{
      .lang{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .menu{
    background: #131a1c;
    z-index: 20;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    padding: 4.5em 1.5em 3em;
    flex-direction: column;
    align-items: flex-start !important;
    overflow-y: auto;
  }

  .header-action{
    &:after{
      display: none;
    }
  }

  .burger{
    margin-left: 1.5em;
  }

  .is-online{
    padding: 0 .75em;
    height: 1.375em;
    .text13{
      font-size: .75em;
    }
  }

  .header-user {
    .m-dropdown__show {
      width: 1.75em;
      height: 1.75em;
      border-radius: .375em;
    }
  }
}
