.deal{
  padding: 7em 0 6.625em;
  &__inner{
    padding: 2em;
    margin-top: 2em;
    background: #1d2427;
    border-radius: 1.25em;
    display: grid;
    grid-template-columns: 24.75em calc(100% - 28.25em);
    gap: 3.75em 3.5em;
  }
}

.deal-progress{
  display: flex;
  margin-top: 2em;
}

.deal-step{
  flex-shrink: 0;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: rgba($white, 0.5);
  &:last-child{
    &:after{
      display: none;
    }
  }
  &._done{
    color: $white;
    .deal-step__num{
      background-color: transparent;
      border: 1px solid rgba($primary,.8);
      background-image: url(../img/check4.svg);
      @include bgImgSize(1.25em);
      span{
        display: none;
      }
    }
  }
  &._active{
    color: $primary;
    .deal-step__num{
      background: $primary;
    }
  }
  &:after{
    content: '';
    flex-shrink: 0;
    width: 1.375em;
    height: 1px;
    background: #323d41;
    margin: 0 .75em;
  }
  &__num{
    margin-right: .75em;
    flex-shrink: 0;
    @include center;
    background: #1c2428;
    width: 2.25em;
    height: 2.25em;
    border-radius: 50%;
    color: $white;
  }
}

.deal-sidebar{
  padding-right: 2.5em;
  border-right: 1px solid #232a2e;
  &__props{
    @include grid100gap(1.125em);
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.line-btn{
  font-size: .8125em;
  line-height: 138%;
  color: $gray;
  border-bottom: 1px solid rgba($gray,.4);
  padding-bottom: .1em;
  &:hover{
    color: $primary;
    border-color: $primary;
  }
}

.deal-prop{
  &__title{
    font-size: .875em;
    line-height: 114%;
    color: #888;
  }
  .pair__item{
    margin-top: .625em;
    &-icon{
      width: 1.25em;
    }
  }
  &__course{
    font-size: 1.125em;
    font-weight: 500;
    margin-top: .56em;
    &-desc{
      opacity: .6;
      font-weight: 500;
      font-size: .875em;
      line-height: 129%;
      margin-top: .5em;
    }
  }
}

.deal-warning{
  margin-top: 2em;
  &__text{
    margin-top: .5em;
    font-size: .9375em;
  }
}

.deal-time{
  padding: 1.25em;
  margin-top: 1.75em;
  background: #292f32;
  border-radius: .625em;
  &__top{
    margin-bottom: .25em;
  }
  &__title{
    font-weight: 500;
    font-size: .875em;
    line-height: 171%;
  }
  &__num{
    font-weight: 500;
    font-size: 1.3125em;
    line-height: 114%;
    color: #ffe6a6;
    margin-bottom: .5em;
  }
  &__desc{
    font-size: .8125em;
    line-height: 138%;
    color: $gray;
  }
}


@media screen and (max-width: 992px){
  .deal{
    padding: 6.25em 0 6em;
    &__inner{
      grid-template-columns: 100%;
      padding: 1.75em;
    }
  }

  .deal-progress{
    margin-top: 1.875em;
  }

  .deal-step{
    &__num{
      width: 2em;
      height: 2em;
    }
    .text15{
      font-size: .8125em;
    }
  }

  .deal-sidebar{
    padding: 1.5em 0 0;
    border-right: none;
    border-top: 1px solid #232a2e;
    position: relative;
    &__props{
      gap: 1em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .deal-prop{
    &__title{
      font-size: .8125em;
    }
    .pair__item{
      .text18{
        font-size: 1em;
      }
    }
    &__course{
      font-size: 1em;
      margin-top: .625em;
      &-desc{
        font-size: .75em;
        margin-top: .33em;
      }
    }
  }

  .deal-warning{
    margin-top: 2.25em;
    &__text{
      font-size: .875em;
    }
  }

  .deal-time{
    @media screen and (min-width: 577px){
      position: absolute;
      width: 22.125em;
      right: 0;
      top: 1.5em;
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .deal-page{
    .footer{
      padding-bottom: 6em;
    }
  }

  .deal{
    padding: 4.5em 0;
    &__inner{
      padding: 1.5em 1.25em;
      margin: .25em 0 0 -1.25em;
      width: calc(100% + 2.5em);
      gap: 2.5em;
    }
  }

  .deal-progress{
    margin: 1.375em 0 0 -1.25em;
    padding: 0 1.25em 1em;
    width: calc(100% + 2.5em);
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
      display: none;
    }
  }

  .deal-step{
    &:after{
      margin: 0 .5em;
      width: .8125em;
    }
  }

  .deal-sidebar{
    padding-top: 1.125em;
  }

  .deal-warning{
    margin-top: 1.375em;
  }

  .deal-time{
    margin: 0;
    z-index: 10;
    padding: 1em 1.625em;
    position: fixed;
    bottom: -.25em;
    left: 0;
    width: 100%;
    box-shadow: 0 -.0625em 1.25em rgba(0, 0, 0, 0.14);
    background: #1e2427;
    border-top: 1px solid #292f32;
    border-radius: 0;
    &__top{
      margin-bottom: .5em;
    }
    &__title{
      font-size: .75em;
    }
    &__num{
      font-size: 1.125em;
      display: flex;
      align-items: center;
      margin: 0;
      &:after{
        content: '';
        flex-shrink: 0;
        width: 1.11em;
        height: 1.11em;
        background-image: url(../img/info.svg);
        @include bgImgSize(100%);
        margin-left: .4em;
      }
      &:not(:hover){
        &~.deal-time__desc{
          opacity: 0;
          pointer-events: none;
        }
      }
    }
    &__desc{
      width: 23.125em;
      padding: 1em;
      box-shadow: 0 .875em 2.75em rgba(0, 0, 0, 0);
      background: #060e13;
      border-radius: .9375em;
      font-size: .75em;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 100%;
      margin-bottom: -3.5em;
    }
  }
}
