.verification-group{
  margin-top: 4.125em;
  &:first-child{
    margin-top: 0;
  }
  &__fields{
    margin-top: 1.5em;
    .m-check{
      color: #808888;
      margin-top: .25em;
    }
  }
  &__tabs{
    display: flex;
    margin-top: 1.5em;
  }
}

.card-upload{
  display: grid;
  grid-template-columns: 13.75em calc(100% - 14.75em);
  gap: 1em;
  margin-top: 1.5em;
  &__qr{
    display: grid;
    grid-template-columns: 8.125em calc(100% - 10.125em);
    gap: 2em;
    align-items: center;
    padding: 1.875em 2.1875em;
    border-radius: 1em;
    background: #292f32;
    img{
      width: 100%;
      display: block;
    }
    &-text{
      @include grid100gap(.5em);
    }
    &-title{
      font-weight: 600;
      font-size: .9375em;
      line-height: 120%;
    }
    &-desc{
      font-size: .875em;
      line-height: 143%;
      color: #91959e;
      button{
        color: #0dac69;
        text-decoration: underline;
      }
    }
  }
}

.file-input{
  flex-shrink: 0;
  position: relative;
  padding-bottom: 83.6%;
  border-radius: 1em;
  background: rgba(41, 47, 50, 0.4);
  border: 1px dashed #3c4447;
  .preview{
    max-width: 100%;
    max-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    position: absolute;
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @include center;
    flex-direction: column;
    text-align: center;
  }
  img{
    width: 1.625em;
    margin-bottom: 1.25em;
  }
  .error-text{
    position: absolute;
    left: 0;
    top: 100%;
    margin-top: .5em;
    font-size: .75em;
    display: none;
  }
  &._error{
    border-color: #f96161;
    background: #29272a;
    .error-text{
      display: block;
    }
  }
}

.upload-success{
  margin-top: 1.5em;
  @include center;
  flex-direction: column;
  padding: 1.75em;
  background: #292f32;
  border: 1px solid #008c52;
  border-radius: 1em;
  &__inner{
    display: flex;
    align-items: center;
    img{
      width: 1.75em;
      margin-right: .5em;
    }
  }
  &__file{
    display: flex;
    align-items: center;
    margin-top: .375em;
    font-weight: 500;
    color: $primary;
    &-remove{
      width: 1.25em;
      @include center;
      margin-left: .5em;
      svg{
        width: 100%;
      }
      &:hover{
        path{
          stroke: $primary;
        }
      }
    }
  }
}

.card-guide{
  margin-top: 2.5em;
  padding: .625em .625em .625em 2.375em;
  background: #191f22;
  border-radius: 1.25em;
  display: grid;
  grid-template-columns: calc(100% - 20.875em) 20.125em;
  gap: 0 .75em;
  align-items: center;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  &__content{
    @include grid100gap(1.125em);
  }
  &__desc{
    line-height: 150%;
    color: #879696;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .verification-group{
    margin-top: 2.5em;
    &__fields{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: .5em;
      .m-check{
        margin-top: .75em;
      }
    }
  }

  .card-upload{
    margin-top: 1.5em;
    grid-template-columns: 100%;
    &__qr{
      grid-template-columns: 5.5em calc(100% - 7em);
      gap: 1.5em;
      padding: 1.375em 1.125em;
      &-title{
        font-size: .875em;
        line-height: 129%;
      }
      &-desc{
        font-size: .8125em;
        line-height: 123%;
      }
    }
  }

  .upload-success{
    padding: 2em;
    margin-top: 1em;
  }

  .file-input{
    padding-bottom: 0;
    height: 3.75em;
    border-radius: .8125em;
    &__inner{
      flex-direction: row;
      img{
        width: 1.375em;
        margin: 0 .75em 0 0;
      }
    }
  }

  .card-guide{
    margin-top: 1.5em;
    grid-template-columns: 100%;
    padding: .375em;
    &__content{
      gap: .75em;
      padding: 1.375em .875em 2.625em;
    }
  }
}
