.m-table{
  &__wrap{
    background-color: #1e2427;
    border: 1px solid #1d2427;
    padding: 1.5em;
    border-radius: 1.75em;
  }
  table{
    width: 100%;
    border-collapse: collapse;
  }
  tbody{
    tr:last-child{
      td{
        padding-bottom: 0;
      }
    }
  }
  th,td{
    font-weight: 400;
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  th{
    color: #797977;
    padding-bottom: 1em;
    text-align: left;
  }
  td{
    padding-top: 1em;
    padding-bottom: 1em;
    border-top: 1px solid #292f32;
  }
}

.m-status{
  font-size: 1.125em;
  line-height: 133%;
  &._green{
    color: #008c52;
  }
  &._red{
    color: #ff5b5b;
  }
}

.exchange-table{
  margin-top: 3em;
  th,td{
    &:nth-child(1),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5){
      width: 13.5em;
      padding-right: 1em;
    }
    &:nth-child(5){
      padding-right: 0;
      padding-left: 1em;
      text-align: right;
    }
  }
}

.exchanges-empty{
  margin-top: 6.625em;
}


@media screen and (max-width: 992px){
  .m-table{
    overflow-x: auto;
    overflow-y: hidden;
    padding: 0 1.5em;
    margin-top: 2em;
    margin-bottom: -1em;
    margin-left: -1.5em;
    width: calc(100% + 3em);
    &::-webkit-scrollbar {
      width: 0.1875em;
      height: 0.1875em;
    }
    &::-webkit-scrollbar-track {
      background: #2d3538;
      border-radius: 5.625em;
    }
    &::-webkit-scrollbar-thumb {
      background: #616A6E;
      border-radius: 5.625em;
    }
    &__wrap{
      border-radius: 1.5em;
      padding: 1.5em;
      margin-bottom: -.1875em;
    }
  }

  .exchange-table{
    .m-table__wrap{
      min-width: 75em;
    }
    th,td{
      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(5){
        width: 11em;
      }
    }
  }

  .exchanges-empty{
    margin-top: 8em;
  }
}


@media screen and (max-width: 576px){
  .m-table{
    padding: 0 1.25em;
    margin-left: -1.25em;
    width: calc(100% + 2.5em);
    &__wrap{
      padding: 1.5em .75em;
    }
  }

  .exchanges-empty{
    margin-top: 5.75em;
  }
}
