.deal-content{
  display: flex;
  flex-direction: column;
  &__top{
    @include grid100gap(.8125em);
    width: 38.5em;
    max-width: 100%;
    &--big{
      width: 40.5em;
    }
  }
  &__hint{
    padding-top: 3.5em;
    margin: auto auto 0;
    font-size: .8125em;
    line-height: 154%;
    color: #7d8786;
    text-align: center;
    max-width: 44em;
  }
}

.card-empty{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 3.5em;
  &__icon{
    margin-bottom: 1.25em;
    width: 3em;
  }
  &__title{
    font-weight: 600;
    font-size: 1.125em;
    line-height: 100%;
  }
  &__btn{
    margin-top: 2em;
    padding: 0 2em;
    height: 3.5em;
    span{
      font-size: .9375em;
    }
    img{
      margin-right: .75em;
      width: 1em;
    }
  }
}

.verification-card{
  padding: 1.375em 1.5em;
  border: 1px solid #2a3134;
  border-radius: 1.125em;
  margin-top: 2.5em;
  display: flex;
  align-items: center;
  &__icon{
    margin-right: 1em;
    flex-shrink: 0;
    width: 1.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    line-height: 154%;
    color: #7d8786;
  }
  &__code{
    padding-left: 1em;
    margin-left: auto;
    font-weight: 700;
    align-self: flex-start;
  }
}

.spinner {
  margin: auto;
  width: 1.75em;
  height: 1.75em;
}

.spinner line {
  stroke: #fff;
  stroke-width: 3;
  animation: .8s linear infinite fade
}

@keyframes fade {
  from {
    opacity: 1
  }
  to {
    opacity: 0
  }
}

.verification-error{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 5.75em;
  &__icon{
    width: 1.9375em;
    margin-bottom: .75em;
  }
  &__text{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    max-width: 23.75em;
  }
  &__btn{
    margin-top: 2.125em;
    padding: 0 1.5em;
  }
}


@media screen and (max-width: 992px){
  .deal-content{
    order: -1;
    &__top{
      gap: .75em;
    }
    &__hint{
      font-size: .75em;
    }
  }

  .card-empty{
    margin-top: 3em;
    &__icon{
      margin-bottom: .75em;
      width: 2.25em;
    }
    &__title{
      font-size: .875em;
      line-height: 129%;
    }
    &__btn{
      margin-top: 1.5em;
      height: 3em;
      span{
        font-size: .875em;
      }
    }
  }

  .verification-card{
    margin-top: 3em;
  }

  .verification-error{
    margin-top: 3.25em;
  }
}


@media screen and (max-width: 576px){
  .card-empty{
    margin-top: 1em;
  }

  .verification-card{
    margin-top: 1.75em;
    padding: 1.375em 1em;
    @include grid100gap(1em);
  }

  .verification-error{
    margin-top: 2.6875em;
  }
}
