.account-settings{
  padding: 3.5em;
  margin: 0 auto;
  width: 40.125em;
  max-width: 100%;
  border-radius: 1.75em;
  box-shadow: 0 .25em .75em rgba(49, 54, 56, 0.05);
  background: #1e2427;
  &__fields{
    margin-top: 2.75em;
    @include grid100gap(.75em);
  }
  .fg{
    input{
      font-size: 1em;
      height: 5em;
      padding: 1.75em 1.5em 0;
    }
    label{
      font-size: 1em;
      left: 1.5em;
    }
  }
  .pass-toggle{
    bottom: 1em;
  }
  &__nav{
    margin-top: 2.75em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    padding: 0 1.5em;
    height: 3.5em;
    span{
      font-size: 1em;
    }
  }
  &__bottom{
    margin-top: 3.75em;
    text-align: center;
  }
  &__hint{
    color: #646e73;
    a{
      color: #008c52;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){
  .account-settings{
    .fg{
      font-size: .875em;
    }
    .pass-toggle{
      font-size: 1.14em;
      bottom: 1.375em;
    }
    &__hint{
      font-size: .875em;
      line-height: 171%;
    }
  }
}


@media screen and (max-width: 576px){
  .account-settings{
    padding: 2.5em 1.25em;
    max-width: unset;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    border-radius: 1.25em;
    &__fields{
      margin-top: 2em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      height: 3em;
      span{
        font-size: .875em;
      }
    }
    &__bottom{
      margin-top: 3em;
    }
  }
}
