.cards{
  position: relative;
  padding: 7.75em 0;
  &__items{
    margin-top: 3.5em;
    @include grid100gap(.5em);
  }
  .info-block{
    margin-top: 6.625em;
  }
}

.card-item{
  display: flex;
  align-items: center;
  padding: 1em .875em 1em 1.5em;
  background: #1e2427;
  border-radius: 1em;
  font-weight: 600;
  &__icon{
    width: 1.5em;
    margin-right: 1em;
  }
  &__title{
    padding-right: 1em;
  }
  &__remove{
    margin-left: auto;
    width: 2em;
    height: 2em;
    background: #292f32;
    border-radius: 50%;
    @include center;
    &:hover{
      background: $primary;
    }
    img{
      width: 1em;
    }
  }
}

.delete-modal{
  &:after{
    display: none;
  }
  .modal__dialog{
    padding: 3em 2em;
  }
  &__desc{
    margin-top: 1em;
  }
  .modal__nav{
    margin: 4.875em auto 0;
    width: 12.5em;
  }
  .modal__btn{
    height: 3.25em;
  }
  .close-btn{
    width: 1.25em;
    position: absolute;
    top: 1.25em;
    right: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .cards{
    padding: 6.75em 0 8.75em;
    &__items{
      margin-top: 2.875em;
    }
    .info-block{
      margin-top: 5.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .cards{
    padding: 5.25em 0 9.75em;
    &__items{
      margin-top: 2.25em;
    }
    .info-block{
      margin-top: 6.25em;
    }
  }

  .card-item{
    padding: 1em .75em;
    border-radius: .75em;
    &__icon{
      width: 1.25em;
    }
    &__title{
      font-size: .875em;
    }
    &__remove{
      width: 1.5em;
      height: 1.5em;
      img{
        width: .8125em;
      }
    }
  }

  .delete-modal{
    .modal__nav{
      margin-top: 3.375em;
    }
  }
}
