.auth{
  display: grid;
  grid-template-columns: 26.125em calc(100% - 26.125em);
  min-height: $vh;
  &--recovery{
    .auth__col2{
      padding-top: 11.5em;
    }
  }
  &__col1{
    background-image: url(../img/auth-bg.webp);
    background-size: cover;
    padding: 1.5em 2em;
  }
  &__col2{
    background-image: url(../img/auth-bg2.webp),url(../img/auth-bg3.svg);
    background-size: cover, 69em auto;
    background-position: center, 0 0;
    background-repeat: no-repeat;
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__back{
    display: flex;
    align-items: center;
    font-weight: 500;
    align-self: flex-start;
    margin-bottom: 6.625em;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 1em;
      margin-right: .375em;
    }
  }
  &__links{
    margin-top: 2.5em;
    display: flex;
    justify-content: space-between;
    &--center{
      justify-content: center;
    }
  }
  &__link{
    color: $primary;
    padding: 0 0 .33em;
    border-bottom: 1px solid rgba($primary,.4);
    &:hover{border-color: $primary;

    }
  }
}

.auth-form{
  max-width: 100%;
  width: 22.5em;
  margin-top: 2em;
  &__fields{
    @include grid100gap(.375em);
  }
  &__btn{
    margin-top: 1.25em;
    height: 3.5em;
    width: 100%;
    span{
      font-size: 1em;
    }
  }
}

.error-text{
  font-weight: 500;
  font-size: .8125em;
  line-height: 146%;
  color: #eb5757;
}

.m-captcha{
  margin-top: 1.5em;
  display: flex;
  justify-content: center;
  &__img{
    margin: 0 auto;
    max-width: 16.375em;
    display: block;
    width: 100%;
  }
  &__error{
    margin: 1em auto 0;
    text-align: center;
  }
}

.m-alert{
  position: fixed;
  right: 1.5em;
  bottom: 2em;
  padding: .9375em;
  background: #343e43;
  border-radius: .5625em;
  display: flex;
  align-items: center;
  font-weight: 500;
  img{
    width: 1.25em;
    margin-right: .625em;
  }
}


@media screen and (max-width: 992px){
  .auth{
    display: block;
    position: relative;
    &--recovery{
      .auth__col2{
        padding-top: 10.25em;
      }
    }
    &__col1{
      background-image: unset;
      width: 100%;
      top: 0;
      left: 0;
      position: absolute;
      padding: 1.5em 1.6875em;
    }
    &__col2{
      min-height: $vh;
      padding: 6.625em 1.25em 1.25em;
    }
    &__back{
      align-self: center;
      margin-bottom: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .auth{
    &--recovery{
      .auth__col2{
        padding-top: 7.5em;
      }
    }
    &__col1{
      padding: .875em 1.25em;
    }
    &__col2{
      padding-top: 4.375em;
    }
    &__logo{
      width: 6.75em;
    }
  }

  .auth-form{
    margin-top: 1.5em;
  }

  .m-alert{
    padding: .9375em 1.375em;
    right: 1em;
    width: calc(100vw - 2em);
  }
}
