.pagination{
  margin-top: 3em;
  display: flex;
  justify-content: center;
  &__item{
    height: 2.67em;
    min-width: 2.67em;
    padding: 0 .22em;
    border-radius: .5em;
    background-color: #1e2427;
    color: #fff;
    @include center;
    margin: 0 .33em;
    font-weight: 500;
    font-size: 1.125em;
    line-height: 133%;
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      color: $white;
      background-color: #008c52;
    }
  }
}


