}


@media screen and (max-width: 576px){
  .pagination{
    ._hide-mob{
      display: none;
    }
    ._btn{
      width: 2.8125em;
      padding: 0;
    }
    &__item{
      //height: 2.8125em;
      //min-width: 2.8125em;
      //padding: 0 .875em;
      span{
        font-size: .875em;
      }
    }
  }
}
