.reward{
  padding: 4.375em 0 3.75em;
  position: relative;
  &__img{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -1.5em;
    width: 78em;
    z-index: -1;
  }
  &__top{
    @include grid100gap(1.5em);
  }
  &__items{
    margin-top: 3em;
    display: grid;
    grid-template-columns: 18.75em 27.625em calc(100% - 46.375em);
  }
}

.reward-item{
  padding-bottom: 2.5em;
  position: relative;
  &._red,
  &._green{
    padding-left: 2.875em;
    padding-right: 2.875em;
  }
  &._red{
    &:after{
      box-shadow: 0 0 .625em #ff602e;
      background: #ff6a3b;
    }
    .reward-item__num{
      color: #ff6b3c;
    }
  }
  &._green{
    &:after{
      box-shadow: 0 0 .625em #13ba75;
      background: #03b36a;
    }
    .reward-item__num{
      color: #06e186;
    }
    .reward-item__desc{
      max-width: 26.5em;
    }
  }
  &:after{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: .375em;
    box-shadow: 0 0 .625em rgba(255, 156, 71, 0.75);
    background: #ffc148;
  }
  &__num{
    font-size: 3em;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffc148;
  }
  &__desc{
    margin-top: .75em;
  }
}

.referral{
  padding: 3.75em 0 9.625em;
  &__inner{
    display: grid;
    grid-template-columns: 34em 40em;
    justify-content: space-between;
    gap: 3.375em;
  }
  &__content{
    @include grid100gap(2.375em);
  }
}

.referral-warning{
  padding: 1.5em 1.5em 2em;
  background: #142226;
  border-radius: 1em;
  &__icon{
    width: 1.5em;
    margin-bottom: 1em;
  }
  &__title{
    font-size: 1.25em;
    line-height: 140%;
    font-weight: 800;
  }
  &__desc{
    margin-top: .5em;
  }
}


@media screen and (max-width: 992px){
  .reward{
    padding: 2.8125em 0 2.5em;
    &__img{
      width: 100%;
    }
    &__top{
      gap: .75em;
    }
    &__items{
      margin-top: 3.75em;
      grid-template-columns: 1fr 1fr 1.1fr;
    }
  }

  .reward-item{
    padding-bottom: 1.8125em;
    &:after{
      height: .25em;
    }
    &._green,
    &._red{
      padding-left: 1.25em;
      padding-right: 0;
    }
    &__num{
      font-size: 2.375em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .referral{
    padding: 2.5em 0 5em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      gap: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .reward{
    padding: 2em 0 2.5em;
    &__img{
      display: none;
    }
    &__items{
      margin-top: 3em;
      grid-template-columns: 100%;
    }
  }

  .reward-item{
    padding-bottom: 0;
    padding-left: 1.875em;
    max-width: 16em;
    &._red,
    &._green{
      padding: 1em 0 1em 1.875em;
    }
    &:after{
      width: .1875em;
      height: 100%;
    }
  }

  .referral{
    padding: 2.5em 0 3em;
    &__inner{
      gap: 2.25em;
    }
  }
}
