.contacts-page{
  .wrapper{
    overflow: hidden;
  }
}

.contacts{
  padding: 6.25em 0 20em;
  min-height: 56.25em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__text{
    margin-top: 2.625em;
    @include grid100gap(1.25em);
    max-width: 38em;
  }
  &__subtitle{
    font-size: 1.5em;
    line-height: 108%;
    font-weight: 700;
  }
  &__desc{
    font-weight: 500;
    font-size: .9375em;
    line-height: 147%;
    button,
    a{
      color: #1bbf70;
    }
  }
  &__inner{
    position: relative;
    padding: 2.5em 9.5em 2.5em 3.75em;
    border-radius: 1.375em;
    margin-top: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      backdrop-filter: blur(2.75em);
      box-shadow: 0 .75em 2.125em rgba(0, 0, 0, 0.04);
      background: rgba(23, 33, 35, 0.68);
      border-radius: inherit;
      z-index: -1;
    }
  }
  &__btn{
    padding: 0 1.6875em;
    height: 3em;
    img{
      width: 1.5em;
      margin-right: .75em;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    color: #1bbf70;
    font-weight: 500;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1px;
      height: 4.75em;
      background-color: rgba($white,.05);
      margin: 0 7em;
    }
    img{
      width: 1.5em;
      margin-right: .5em;
    }
    span{
      font-size: 1.125em;
      line-height: 133%;
      padding: 0 .125em;
      border-bottom: 1px solid rgba(#1bbf70,.5);
    }
    &--tg{
      font-weight: 400;
      img{
        width: 2em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 6em 0 8.75em;
    min-height: unset;
    &__text{
      margin-top: 2.375em;
    }
    &__inner{
      flex-wrap: wrap;
      padding: 2.25em 4.5625em;
      justify-content: center;
    }
    &__btn{
      width: 100%;
      height: 3em;
      margin: 0 0 2em;
    }
    &__link{
      &:before{
        height: 3em;
      }
      &--mail{
        &:before{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    padding: 5em 0 4em;
    &__text{
      margin-top: 1.75em;
      gap: .75em;
    }
    &__subtitle{
      font-size: 1.125em;
      line-height: 144%;
    }
    &__desc{
      font-size: .875em;
      line-height: 157%;
    }
    &__inner{
      border-radius: 1em;
      padding: 2em 1.75em 4em;
      flex-direction: column;
      align-items: center;
    }
    &__btn{
      margin-bottom: 0;
    }
    &__link{
      margin: 1.5em 0 0;
      &:before{
        display: none;
      }
      span{
        font-size: 1em;
      }
    }
  }
}
