.tabs{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.375em;
}

.tab{
  height: 2em;
  padding: 0 1em;
  display: flex;
  align-items: center;
  position: relative;
  border-radius: .5em;
  font-weight: 600;
  transition: all .3s;
  background: rgba(69, 80, 86, 0.38);
  margin: .375em .375em 0 0;
  span{
    font-size: .8125em;
    line-height: 120%;
  }
  &:hover{
    background: $primary;
    cursor: pointer;
  }
  &._active{
    background: $primary;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.tab2{
  padding-bottom: .625em;
  border-bottom: 2px solid transparent;
  color: rgba($white,.5);
  margin-right: 1.6875em;
  font-weight: 500;
  transition: all .3s;
  &:last-child{
    margin-right: 0;
  }
  span{
    font-size: .9375em;
    line-height: 133%;
  }
  &:hover{
    color: #1bbc79;
    cursor: pointer;
  }
  &._active{
    color: #1bbc79;
    border-color: #1bbc79;
  }
}



@media screen and (max-width: 576px) {
  .tab2 {
    margin-right: 1.625em;
    padding-bottom: .5em;
    span {
      font-size: .875em;
    }
  }
}
