.reviews{
  padding: 4.125em 0 7.5em;
  background-image: url(../img/reviews-bg.svg);
  background-repeat: no-repeat;
  background-position: center top 5.125em;
  background-size: 78em auto;
  &__top{
    max-width: 60em;
  }
  &__items{
    margin-top: 3.75em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 4.5em;
  }
}

.review-card{
  padding: 1.5em 2em;
  border-radius: 1.375em;
  backdrop-filter: blur(2.75em);
  background: rgba(23, 33, 35, 0.68);
  min-height: 15.5em;
  display: flex;
  flex-direction: column;
  .rating-input{
    margin-bottom: .8125em;
  }
  &__name{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
  }
  &__desc{
    font-weight: 500;
    font-size: 1.375em;
    line-height: 136%;
    margin-top: .9em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
    max-width: 30.75em;
  }
  &__date{
    color: #838f93;
    font-weight: 500;
  }
}

.rating {
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.125em;
    margin-right: .3125em;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: #19e891;
      }
      &~.rating-input__item {
        path {
          fill: #19e891;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: #19e891;
  }
}

.pair{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    font-weight: 500;
    &-icon{
      width: 1.625em;
      display: block;
      flex-shrink: 0;
      margin-right: .375em;
    }
  }
  &__arrows{
    width: 1em;
    margin: 0 .5em;
  }
}


@media screen and (max-width: 992px){
  .reviews{
    background-image: unset;
    padding: 2.5em 0 3.375em;
    &__top{
      max-width: 31.25em;
    }
    &__items{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
    &__nav{
      margin-top: 3em;
    }
  }

  .review-card{
    padding: 1em;
    border-radius: .75em;
    &__name{
      font-size: 1em;
      line-height: 125%;
    }
    &__desc{
      margin-top: 1em;
      font-size: 1em;
      line-height: 137%;
    }
  }

  .rating-input{
    &__item{
      width: 1em;
      margin-right: .25em;
    }
  }

  .pair{
    &__item{
      &-icon{
        width: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .reviews{
    padding: 2em 0 4.125em;
    &__items{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      width: 100%;
    }
  }

  .review-card{
    min-height: 14em;
  }
}
