.info{
  padding: 7em 0 9.5em;
  position: relative;
  z-index: 2;
  .text{
    margin-top: 4.375em;
  }
  &--p0{
    .text{
      p{
        @include lastM(0);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .info{
    padding: 6em 0 8em;
    .text{
      margin-top: 5.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    padding: 5em 0;
    .text{
      margin-top: 1.75em;
    }
  }
}
