.reviews2{
  padding: 6.25em 0 5em;
  &__top{
    @include grid100gap(1.25em);
  }
  &__items{
    margin-top: 3.3125em;
    column-count: 3;
    column-gap: 1em;
    .review-card{
      display: inline-flex;
      width: 100%;
      margin-bottom: .75em;
      &__name{
        font-size: 1.0625em;
      }
      &__desc{
        font-size: .9375em;
        line-height: 173%;
      }
      &__bottom{
        padding-top: 3.5em;
      }
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 5em;
  }
  &__btn{
    height: 4.25em;
    width: 21.75em;
    max-width: 100%;
    span{
      font-size: 1em;
    }
  }
}

.feedback{
  padding: 5em 0;
  &__inner{
    padding: 3em 2.8125em;
    background-color: #0b1e1f;
    background-image: url(../img/feedback-bg.svg);
    background-position: 0 0;
    background-repeat: no-repeat;
    border-radius: 1.5em;
    background-size: auto 100%;
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 4.1875em;
    margin-right: 3em;
  }
  &__content{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__text{
    @include grid100gap(.5em);
  }
  &__title{
    font-weight: 600;
    font-size: 1.625em;
    line-height: 115%;
    span,
    a{
      color: #04cd7a;
    }
  }
  &__desc{
    font-size: 1em;
    line-height: 187%;
    font-weight: 500;
  }
  &__btn{
    padding: 0 3.75em;
    height: 3.5em;
    span{
      font-size: 1em;
    }
  }
}

.review-modal{
  padding: 0;
  .modal__dialog{
    position: fixed;
    height: calc(100 * var(--vh) - 1em);
    width: 40.75em;
    max-width: calc(100% - 1em);
    right: .5em;
    top: .5em;
    overflow-y: auto;
    padding: 2.625em;
  }
  .close-btn{
    top: 1.25em;
    right: 1.25em;
    width: 1.5em;
    position: absolute;
  }
  &__top{
    @include grid100gap(1em);
  }
}

.review-form{
  margin-top: 2.5em;
  &__score,
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .75em .5em;
  }
  &__anonymous{
    display: flex;
    align-items: center;
    margin-top: 1.75em;
    .m-switch{
      margin-right: .75em;
    }
  }
  &__score{
    margin-top: 2.25em;
  }
  &__btn{
    margin: 2em auto 0;
    height: 3.5em;
    padding: 0 3.5em;
    span{
      font-size: 1em;
    }
  }
}

.score-field{
  label{
    @include center;
    height: 4em;
    background-color: #232b2f;
    border-radius: 3.75em;
  }
  input:checked~label{
    background-color: rgba($primary,.2);
  }
}


@media screen and (max-width: 992px){
  .reviews2{
    padding-top: 6em;
    &__top{
      gap: 1em;
    }
    &__items{
      margin-top: 3.625em;
      column-gap: .5em;
      column-count: 2;
      .review-card{
        margin-bottom: .5em;
        &__name{
          font-size: 1em;
        }
        &__desc{
          font-size: 1em;
        }
        &__bottom{
          padding-top: 3em;
        }
      }
    }
    &__nav{
      margin-top: 3em;
    }
    &__btn{
      height: 3.5em;
      span{
        font-size: .875em;
      }
    }
  }

  .feedback{
    &__inner{
      padding: 2.25em 2.8125em;
      align-items: flex-start;
    }
    &__icon{
      width: 2.625em;
      margin-right: 2em;
    }
    &__content{
      flex-direction: column;
      align-items: flex-start;
    }
    &__text{
      gap: .375em;
    }
    &__title{
      font-size: 1.375em;
    }
    &__btn{
      margin-top: 1.25em;
      padding: 0 2.1875em;
      height: 3em;
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .reviews2{
    padding: 5em 0 1.5625em;
    &__items{
      margin-top: 1em;
      column-count: 1;
      .review-card{
        &__bottom{
          padding-top: 2em;
        }
      }
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__btn{
      width: 100%;
    }
  }

  .feedback{
    padding: 1.5625em 0 4.625em;
    &__inner{
      padding: 2.1875em 1.75em 1.875em;
      display: block;
    }
    &__logo{
      display: block;
      margin: 0 0 1.75em .25em;
    }
    &__text{
      gap: 1em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 125%;
    }
    &__desc{
      font-size: .875em;
      line-height: 143%;
    }
    &__btn{
      margin-top: 3.25em;
      width: 100%;
    }
  }

  .review-modal{
    .modal__dialog{
      border-radius: 1em;
      padding: 2.125em .75em;
    }
    .close-btn{
      top: 1.125em;
      right: 1.125em;
    }
    &__top{
      gap: .5em;
    }
  }

  .review-form{
    margin-top: 2em;
    &__fields{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }
}
